<template>
  <div class="counter">
    {{ count }}
    <div class="counter__button" @click="increment">+</div>
  </div>
</template>

<script>
import store from './store/NotificationStore'

export default {
  name: 'counter',
  data () {
    return {
      state: store.state 
    }
  },
  computed: {
    count: function () { return this.state.count }
  },
  methods: {
    increment: function () {
      store.increment()
    }
  }
}
</script>

<style lang="scss">

</style>



